:root{
  --green-color:#3eb969;
  --blue-color:#38b6ff;
  --orange-color:#ff914d;
  --purple-color:#ff66c4;
  --dark-purple-color:#CB6CE6;
  --red-color:#ff1616;
  --yellow-color:#ffde59;
  --black-color:rgba(0,0,0);
}
@font-face{
  font-family: mokoto;
  src: url(./fonts/Mokoto/Mokoto\ Glitch.otf);
}
.greenC{
    color: var(--green-color);
}
.blueC{
    color: var(--blue-color);
}
.orangeC{
    color: var(--orange-color);
}
.purpleC{
    color: var(--purple-color);
}
.redC{
    color: var(--red-color);
}
.yellowC{
    color: var(--yellow-color);
}
.blackC{
    color: var(--black-color);
}

body{
    background-color: var(--black-color);
    font-family: 'mokoto';
}
.mainFont{
  font-family: 'mokoto';
}
.bg{
    /* background-color: var(--black-color); */
}
.newHudOrderBtn,.newHudOrderBtn:focus,.newHudOrderBtn:active{
    background-color: var(--purple-color) !important;
    color: white !important;
    border-radius: 15px;
    font-weight: bold;
    border-color: var(--purple-color) !important;
}
.hudCrossBtn,.hudCrossBtn:focus,.hudCrossBtn:active{
    background-color: transparent !important;
    position: absolute;
    right: 0;
    top: 0;
    border:1px solid white !important;
    border-radius: 0 !important;
    border-top: none !important;
    border-right: none !important;
    box-shadow: none !important;
}
.hudCrossBtn:focus{
    border-color: #ff66c4 !important;
    color: #ff66c4 !important;
}
.simpleTab.MuiBox-root>.MuiTabs-root>.MuiTabs-scroller>.MuiTabs-flexContainer{
    display: flex;
    justify-content: space-around;
}
.simpleTab.MuiBox-root>.MuiTabs-root>.MuiTabs-scroller>.MuiTabs-flexContainer>.MuiButtonBase-root,
.countinueBtn{
    outline: 5px auto white;
    color: white;
    border: 5px solid white;
    border-radius: 15px;
    font-family: 'mokoto';
    font-weight: 800;
}
.simpleTab.MuiBox-root>.MuiTabs-root>.MuiTabs-scroller>.MuiTabs-flexContainer>.MuiButtonBase-root:focus{
    outline: 5px auto #1976d2;
    border: 2px solid #1976d2;
}
.countinueBtn:focus{
    outline: 5px auto white;
    border: 5px solid white;
}
.simpleTab.MuiBox-root>.MuiTabs-root>.MuiTabs-scroller>.MuiTabs-flexContainer>.MuiButtonBase-root.Mui-selected ,
.countinueBtn ,
.countinueBtn:hover,
.countinueBtn:active{
    outline: 5px auto white ;
    border: 5px solid white !important;
    background-color: #38B6FF !important;
    color: black !important;
}
/* colored tabs */
.coloredTab.MuiBox-root>.MuiTabs-root>.MuiTabs-scroller>.MuiTabs-flexContainer>.MuiButtonBase-root.firstTab{
    border :5px solid var(--green-color);
}
.coloredTab.MuiBox-root>.MuiTabs-root>.MuiTabs-scroller>.MuiTabs-flexContainer>.MuiButtonBase-root.SecondTab{
    border :5px solid var(--blue-color);
}
.coloredTab.MuiBox-root>.MuiTabs-root>.MuiTabs-scroller>.MuiTabs-flexContainer>.MuiButtonBase-root.thirdTab{
    border :5px solid var(--yellow-color);
}
.coloredTab.MuiBox-root>.MuiTabs-root>.MuiTabs-scroller>.MuiTabs-flexContainer>.MuiButtonBase-root.forthTab{
    border :5px solid var(--purple-color);
}
.coloredTab.MuiBox-root>.MuiTabs-root>.MuiTabs-scroller>.MuiTabs-flexContainer>.MuiButtonBase-root.fifthTab{
    border :5px solid var(--red-color);
}
.coloredTab.MuiBox-root>.MuiTabs-root>.MuiTabs-scroller>.MuiTabs-flexContainer>.MuiButtonBase-root.SisthTab{
    border :5px solid var(--orange-color);
}
.coloredTab.MuiBox-root>.MuiTabs-root>.MuiTabs-scroller>.MuiTabs-flexContainer{
    display: flex;
    justify-content: space-around;
}
.coloredTab.MuiBox-root>.MuiTabs-root>.MuiTabs-scroller>.MuiTabs-flexContainer>.MuiButtonBase-root{
    color: #505050;
    border-radius: 15px;
    font-family: 'mokoto';
    font-weight: 800;
    outline: none !important;
}
.coloredTab.MuiBox-root>.MuiTabs-root>.MuiTabs-scroller>.MuiTabs-flexContainer>.MuiButtonBase-root:focus{
    outline: 5px auto #1976d2;
    border: 2px solid #1976d2;
}
.coloredTab.MuiBox-root>.MuiTabs-root>.MuiTabs-scroller>.MuiTabs-flexContainer>.MuiButtonBase-root.Mui-selected{
    outline: 5px auto white;
    border: 5px solid white;
    background-color: #3eb969;
    color: black !important;
}

/*  */
.progressItem{
    font-family: 'mokoto';
    background: transparent;
}
.para, .brandG{
    color: white !important;
    font-family: 'mokoto';
}
.roadMapText{
    font-family: 'mokoto';
    line-height: 38px;
}
.navbar {
 /* padding: 1rem 2rem;
 height: 150px; */
}
.navbar-brand{
    /* font-family: 'mokoto';
    font-size: 70px; */
}
.navbar-collapse{
    border-left: 2px solid white;
    padding: 1rem 1rem;
}
.navbar-toggler {
    background-color: white !important; 
}
.collapse.show {
    display: block;
    z-index: 1;
    background-color: black;
}
.navItem > a{
   font-family: 'mokoto';
   font-size: 25px;
   color: white;
}
.navItem > a:hover{
    color: #3eb963 !important;
}
a.MINT.active{color: var(--green-color) !important;}
a.ABOUT.active{color: var(--blue-color) !important;}
a.ROADMAP.active{color: var(--orange-color) !important;}
a.TEAM.active{color: var(--red-color) !important;}
a.FAQS.active{color: var(--purple-color) !important;}
.twt{
    font-size: 25px;
}
.shapeContainer{
    min-height: 78vh;
    display: flex;
    align-items: center;
    justify-content: start;
}
.shape{
    clip-path: polygon(0 0, 70% 15%, 70% 85%, 0% 100%);
    height: 90%;
    /* background-color: var(--green-color); */
}
.mainTitle{
    font-family: 'mokoto';
    font-size: 130px;
    margin-top: -60px;
    margin-bottom: 0;
}
.mainTitle > .span1{ color:var(--green-color);}
.mainTitle > .span2{ color:var(--blue-color);}
.mainTitle > .span3{ color:var(--orange-color);}
.mainTitle > .span4{ color:var(--purple-color);}
.mainTitle > .span5{ color:var(--red-color);}
.mainTitle > .span6{ color:var(--yellow-color);}
.minted{ font-size: 30px;}
.mintBtn{ font-family:'mokoto';font-size: 40px;padding: 0px 15px;background-color:var(--green-color);
          border-color:var(--green-color);color: var(--black-color);}
/* about page */
.joinBtn{ 
    font-family:'mokoto';
    font-size: 40px;
    padding: 5px 15px;
    background:var(--green-color);
    border-radius: 20px;
    border:5px solid #505050 !important;
    color: var(--black-color);
}
.yrm{
    font-family:'mokoto';
    font-size: 30px;
    padding: 5px 15px;
    background:var(--purple-color);
    border-radius: 20px;
    border:5px solid white !important;
    color: white;
}
.titleShop{
    font-size: 80px !important;
}
.aboutContainer{
    display: flex;
    flex-direction: column;
    height: 78vh;
}
.conectShop{
  font-family:'mokoto';
  font-weight: 800;
  font-size: 14px;
  color: #505050;
}
.aboutHeading{
