    background: transparent !important;
}
.comingSoon{
    font-family: 'mokoto';
    color: white;
    font-size: 4rem;
}
.orderAmount{
   font-size: 30px;
    font-weight: 700; 
}
.orderNumPara{
    font-family: 'mokoto';
