    /* font-family: 'mokoto'; */
    font-weight: 600;
}
.inputStyle,.textAreaStyle,.newHudInput{
    border: 5px solid #A6A6A6;
    background-color: black;
    border-radius: 14px;
    color: white;
    padding-left: 10px;
}
.profileHead{
    font-family: 'mokoto';
    color: white;
    font-size: 22px;
    font-weight: 800;
    text-align: center;
}
.textAreaStyle::placeholder,.shipingDetalHeading{
    padding-left: 10px;
    color: white;
    font-size: 18px;
    font-weight: 800;
    text-align: center;
}
.wheelLeftPara{
  color: white;
  font-size: 15px;
