  text-align: center;
  font-family: 'mokoto';
}
.wheelLeftBtn, .wheelLeftBtn:hover , .wheelLeftBtn:focus , .wheelLeftBtn:active{
  font-size: 25px;
  font-weight: 800;
  text-align: center;
