  border: 5px solid white !important;
  outline: none;
  padding: 8px 10px;
  background-color: #5E17EB !important;
  border-radius: 14px;
}
.newHudInput::placeholder{
    padding-left: 10px;
    color: white;
    font-weight: 800; 
    text-align: center;  
}
.inputStyle::placeholder{
    padding-left: 10px;
    color: white;
    font-weight: 800;

}
/* roadmap page */
.endGame{
    background-image: url('./images/11.png');
    background-size: cover;
    background-position: center;
    height: 100vh;
}

/* after changing */
.nWalletBtn,.nWalletBtn:hover,.nWalletBtn:focus{
  font-family: 'mokoto';
  background-color: #C7D0D8;
  border-color: #C7D0D8;
  box-shadow: none;
  font-size: 13px;
}

.navContainer{
    padding: 5px;
    background-color: green;
    padding-top: 0;
    clip-path: polygon(0 0, 100% 0, 100% 65%, 95% 100%, 5% 100%, 0 65%);
}
.innerNavC{
    background-color: black;
    clip-path: polygon(0 0, 100% 0, 100% 65%, 95% 100%, 5% 100%, 0 65%);
    padding: 15px;
}
.newNavBtn,.newNavBtn:hover,.newNavBtn:focus,.newNavBtn:active{
  background-color: transparent !important;
  outline: none !important;
  border-color: #505050 !important;
  box-shadow: none !important;
}

.newNavBtn.home.active{color: var(--green-color) !important;}
.newNavBtn.roadmap.active{color: var(--orange-color) !important;}
.newNavBtn.about.active{color: var(--blue-color) !important;}
.newNavBtn.faqs.active{color: var(--purple-color) !important;}
.newNavBtn.hud.active{color: var(--yellow-color) !important;}
.newNavBtn.rewards.active{color: var(--dark-purple-color) !important;}
.newNavBtn.team.active{color: var(--red-color) !important;}
.newNavBtn.docs.active{color: var(--purple-color) !important;}

.newNavBtnUB{
  border-radius: 0;
  border: none;
  border-right: 1px solid #505050;
  border-bottom: 1px solid #505050;
}
.newNavBtnBB{
  border-radius: 0;
  border: none;
  border-right: 1px solid #505050;
}
.newNavBtnUB.faqs,.newNavBtnBB.docs{
  border-right: none;
}
.FAQSpadding{
    padding:2rem 10rem;
}
.cursoS{
    cursor: pointer;
}
.headImg{
    max-height: 150px;
    max-width: 150px;
}
.hudProfHead{
    letter-spacing: 1.1rem;
}
@media(max-width:1123px){
   .FAQSpadding{
    padding:0;
   }
    .hudProfHead{
        letter-spacing: 0.1rem;
    }
}
@media(max-width:1106px){
    .nWalletBtn{
        font-size: 10px;
    }
}
@media(max-width:992px){
    .navItem{
        height: auto;
    } 
}
@media(max-width:899px){
    .nWalletBtn{
        font-size: 12px;
    }
}
@media(max-width:810px){
    .shapeContainer,.arrow{
        display: none;
    }
    .mainTitle{
        font-size: 90px;
        margin-top: 0;
    }
    .navbar {
        height: 100px;
    }
    .mintContainer{
        display: flex;
        justify-content: center;
    }
   
    .aboutContainer{
        height: auto;
        margin-top: 20px;
    }
}
@media(max-width:530px){
    .aboutHeading {
        font-size: 30px;
    }
    .innerNavC {
        padding: 0px;
    }
    .joinBtn{
        font-size: 1rem;
    }
    .mainTitle{
        font-size: 63px;
    }
    .navbar {
        height: 110px;
    }
    .newNavBtn{
        font-size: 14px;
    }
}
